import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { CreditType, RescheduleType } from '@nequi/features/credits/core';
import { PresentationPocket } from '@nequi/features/money-management/presentation';
import { ConfirmInfoBottomSheetService, LoadingService } from '@nequi/ui';
import { toCamelCaseProperties } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { AdjustInstallmentsService } from '../../../../services/adjust-installments.service';
import { AdjustInstallmentsConfig } from './adjust-installments.config';
import { AdjustInstallmentsViewModel } from './adjust-installments.view-model';

@Component({
  selector: 'app-parachute-adjust-installments',
  templateUrl: './adjust-installments.page.html',
  styleUrls: ['./adjust-installments.page.scss'],
})
export class AdjustInstallmentsPage extends FeatureBasePage implements OnInit {
  public config = AdjustInstallmentsConfig;
  public viewModel = new AdjustInstallmentsViewModel();
  public i18n: Record<string, any>;
  public flowType = FlowType;
  public pocket: PresentationPocket;

  constructor(
    injector: Injector,
    public adjustInstallmentsService: AdjustInstallmentsService,
    private loadingService: LoadingService,
    private translateProvider: TranslateProvider,
    private datePipe: DatePipe,
    private currencyPipe: CurrencyPipe,
    private confirmInfoBottomSheetService: ConfirmInfoBottomSheetService,
    private router: Router
  ) {
    super(injector);
  }

  public async ngOnInit() {
    this.setPocket();
    this.initI18n();
    await this.loadData();
  }

  public async loadData() {
    try {
      this.viewModel.hasError = false;

      await this.loadingService.show();
      await this.adjustInstallmentsService.loadData();
      await this.loadingService.hide(true);
    } catch (error) {
      await this.loadingService.hide(false);

      this.viewModel.hasError = true;
      this.viewModel.bussyIndicatorError = error.message;
    }
  }

  public async showConfirmationInfo(params: {
    isSuccess: boolean;
    rescheduleType: RescheduleType;
  }) {
    this.viewModel.rescheduleType = params.rescheduleType;

    const buildItemParams = {
      messages: this.i18n.confirmation,
      pocketInfo: this.pocket,
      currencyPipe: this.currencyPipe,
      datePipe: this.datePipe,
      paymentAmount: this.adjustInstallmentsService.paymentAmount,
      nextFeeDate: this.adjustInstallmentsService.data.nextFeeDate,
    };

    const { data } = await this.confirmInfoBottomSheetService.show({
      title: this.i18n.confirmation.title,
      subtitle: this.i18n.confirmation.parachute.subtitle,
      items: this.viewModel.buildItems(buildItemParams),
      confirmLabel: this.i18n.confirmation.buttons.next,
      cancelLabel: this.i18n.confirmation.buttons.cancel,
      viewCancel: true,
      footerDescription: createFormat(this.i18n.confirmation.parachute.footerDescription, [
        this.datePipe.transform(this.adjustInstallmentsService.data.lastFeeDate),
      ]),
    });

    if (data === true) {
      this.adjustInstallmentsService.makePayment(
        this.i18n,
        this.viewModel.rescheduleType,
        this.pocket,
        CreditType.PARACHUTE
      );
    }
  }

  private async initI18n() {
    const result = this.translateProvider.get(this.config.i18n.content);

    this.i18n = toCamelCaseProperties(result);
  }

  private setPocket() {
    this.pocket = this.router.getCurrentNavigation().extras.state?.pocket;
  }
}
