import { Injectable } from '@angular/core';
import {
  CreditType,
  CustomerCreditsEntity,
  CUSTORMER_WITHOUT_PREAPROVED_ERRORS,
  ErrorCode
} from '@nequi/features/credits/core';
import { openUrl } from '@nequi/utils/functions';

import { BaseMiddleware } from './base.middleware';
import { ModalParams } from './modal.params';

@Injectable()
export class ParachuteMiddleware extends BaseMiddleware {
  protected async getCustomerCreditsFail(error): Promise<boolean> {
    this.dynatraceService.setCustomError(error);

    let result = false;

    if (
      error.code === ErrorCode.ISNT_POSSIBLE ||
      CUSTORMER_WITHOUT_PREAPROVED_ERRORS.includes(error.code)
    ) {
      return this.showCannotSaveYouCardModal();
    }

    if (error.code === ErrorCode.EXPLORE_THE_SEA) {
      result = await this.showWelcome();

      return result ? this.showOtherCreditOptions(error.data) : result;
    }

    await this.showComeBackLaterAlert(error.message);

    return result;
  }

  protected setGetCustomerCreditsException() {
    this.getCustomerCreditException = {};
  }

  protected async showOtherCreditOptions(data: CustomerCreditsEntity): Promise<boolean> {
    let i18nKey = this.config.parachute.modal.cannotSaveYou;
    let url: string;

    const [creditType] = data?.availableProducts;

    if (creditType === CreditType.LIFEGUARD) {
      i18nKey = this.config.parachute.modal.lifeguard;
      url = this.config.routes.lifeguard;

      return this.showCardModal<boolean>({
        i18nKey: i18nKey,
        onAccept: () => (url ? this.navController.navigateRoot(url) : false),
        onCancel: () => false,
      } as ModalParams);
    }

    return this.showCannotSaveYouCardModal();
  }

  private async showCannotSaveYouCardModal() {
    const messages = this.translateProvider.get(this.config.parachute.modal.cannotSaveYou);

    const result = await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.description,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: () => false,
        },
        {
          isLink: true,
          text: messages.link.text,
          handler: () => {
            openUrl(messages.link.url);

            return false;
          },
        },
      ],
    });

    return result?.data as Promise<boolean>;
  }
}
