import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import {
  AnalyticsInteractor,
  AnalyticsType,
  NOT_BALANCE_AVAILABLE_ERRORS,
  Transaction
} from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  AsyncLoanPaymentException,
  CreditEventsHandler,
  CreditType,
  OperationCreditType,
  PayCreditParam,
  PaymentTransaction,
  PaymentType as PaymentCreditType,
  RescheduleType
} from '@nequi/features/credits/core';
import { BalanceInteractor } from '@nequi/features/money-management/core';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues, toCamelCaseProperties } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { PaymentConfig } from '../../../../organisms/payment/payment.config';
import { PaymentType } from '../../../../organisms/payment/payment.type';
import { PaymentViewModel } from '../../../../organisms/payment/payment.view-model';
import { CreditInfoModel } from '../../../../services/credit-info.model';
import { PaymentService } from '../../../../services/payment.service';
import { ParachutePaymentConfig } from './payment-home.config';

@Component({
  selector: 'app-credits-propeller-payment',
  templateUrl: './payment-home.page.html',
  styleUrls: ['./payment-home.page.scss'],
})
export class PaymentHomePage extends TransactionBasePage implements OnInit {
  public paymentConfig = PaymentConfig;
  public config = ParachutePaymentConfig;
  public feePaymentMessage: string;
  public clickStatus = true;
  public i18n: Record<string, any>;
  public availableMoney: number;
  public errorMessage;
  public customerProduct;

  constructor(
    public viewModel: PaymentViewModel,
    private currencyPipe: CurrencyPipe,
    private datePipe: DatePipe,
    private creditInfoModel: CreditInfoModel,
    private balanceInteractor: BalanceInteractor,
    private paymentService: PaymentService,
    private creditEventsHandler: CreditEventsHandler,
    private analyticsInteractor: AnalyticsInteractor,
    injector: Injector
  ) {
    super(injector);
    this.viewModel.setConfig(this.paymentConfig);
  }

  public async ngOnInit() {
    await this.initPage();
    this.initI18n();
    this.updateBackpackIcon();

    this.setAnalyticsIntentionToPay();
  }

  public onShowDetailPressed() {
    this.navigatorProvider.navigateForward(this.config.routes.paymentDetail, {
      state: this.creditInfoModel.customerCreditInfo.credit,
    });
  }

  public onPaymentPressed() {
    if (this.availableMoney < this.viewModel.paymentControl.value) {
      this.paymentService.toastService.showError(this.errorMessage.noHaveAvailable);

      this.setAnalyticsIntentionToPay(new CoreException(this.errorMessage.noHaveAvailable, ''));

      return;
    }

    if (
      this.viewModel.selectedPayType === PaymentType.PAYMENT &&
      this.viewModel.paymentTime === PaymentType.PAYMENT
    ) {
      this.paymentService.showModalPayment(this.paymentConfig.i18n.modal.payment, () =>
        this.goToAdjustInstallment()
      );

      return;
    }

    this.viewModel.paymentAmount =
      this.viewModel.paymentAmount || Number(this.viewModel.paymentControl.value);

    this.onConfirmation();
  }

  protected async onTransactionSuccess(): Promise<void> {
    const customKey = `${this.viewModel.paymentTime}-${this.viewModel.selectedPayType}`;

    if (Object.hasOwnProperty.bind(this.paymentConfig.successfulTransactionValidation, customKey)) {
      const i18nKey = this.paymentConfig.successfulTransactionValidation[customKey];

      const i18n = this.translateProvider.get(i18nKey);

      const i18nFormatted = toCamelCaseProperties<Record<string, string>>(i18n);

      this.showMessageSuccess(i18nFormatted, customKey);
    }
  }

  protected onTransactionFailed(error: CoreException): void {
    this.setAnalyticsIntentionToPay(error);

    if (error instanceof ExceedCeilingException) {
      throw error;
    }

    if (error instanceof AsyncLoanPaymentException) {
      this.asyncLoanPaymentFailed();
    }

    if (NOT_BALANCE_AVAILABLE_ERRORS.includes(error.code)) {
      throw error;
    }

    this.paymentService.toastService.showError(error.message);
  }

  protected async isInsufficientMoney(transaction: Transaction): Promise<boolean> {
    if (typeof transaction.pocket?.amount === this.paymentConfig.typeOfNumber) {
      return transaction.pocket?.amount < transaction.amount;
    }

    return false;
  }

  private async initPage() {
    this.customerProduct = this.creditInfoModel.customerCreditInfo;
    this.viewModel.credit = this.customerProduct.credit;
    this.viewModel.setCreditInfo();
    this.viewModel.initForm();

    this.getAvailableMoney();

    await this.getPageConfiguration();
  }

  private async getAvailableMoney() {
    const response = await this.balanceInteractor.getBalance();

    this.availableMoney = response.available;
  }

  private async getPageConfiguration() {
    this.errorMessage = this.translateProvider.get(this.paymentConfig.i18n.error);

    await this.viewModel.getPageConfiguration(this.errorMessage, this.config.i18n.modal.overdueFee);
  }

  private goToAdjustInstallment() {
    this.creditInfoModel.creditFeatureInfo = {
      productId: this.customerProduct.credit.debenture,
      paymentAmount: this.viewModel.paymentControl.value,
      installment: this.customerProduct.credit.installment.totalPending,
    };

    this.navigatorProvider.navigateForward(this.paymentConfig.routes.parachute.adjunstInstallment, {
      state: { pocket: this.viewModel.pocketControl.value },
    });
  }

  private async onConfirmation(): Promise<void> {
    try {
      this.i18n.confirmation.subtitle = this.i18n.confirmation.parachute.subtitle;
      this.i18n.confirmation.footerDescription = this.i18n.confirmation.parachute.footerDescription;

      this.paymentService.showConfirmationInfo(
        this.i18n.confirmation,
        this.viewModel.buildItems(
          this.i18n.confirmation,
          this.viewModel.pocketControl.value,
          this.currencyPipe,
          this.datePipe
        ),
        createFormat(this.i18n.confirmation.footerDescription, [
          this.datePipe.transform(this.viewModel.lastPayment),
        ]),
        () => this.makePayment()
      );
    } catch (error) {
      this.setAnalyticsIntentionToPay(error);
      await this.paymentService.toastService.showError(error.message);
    }
  }

  private initI18n() {
    const result = this.translateProvider.get(this.paymentConfig.i18n.content);

    this.i18n = toCamelCaseProperties(result);
  }

  private async makePayment(): Promise<void> {
    const data: PayCreditParam = {
      operatorType: OperationCreditType.PAYMENT,
      productId: CreditType.PROPELLER,
      debenture: this.customerProduct.credit.debenture,
      paymentType: PaymentCreditType.LOAN,
      rescheduleType: RescheduleType.INSTALLMENTS_NUMBER,
    };
    const transaction = new PaymentTransaction()
      .withData(data)
      .withAmount(this.viewModel.paymentAmount)
      .withPocket(this.viewModel.pocketControl.value);

    super.executeTransaction(transaction);
  }

  private asyncLoanPaymentFailed(): void {
    const fullscreenModalConfig = {
      cssClass: this.paymentConfig.fullscreenModal.cssClass,
      cssClassButton: this.paymentConfig.fullscreenModal.cssClassButton,
    };

    if (
      this.viewModel.paymentTime === PaymentType.PAYMENT &&
      this.viewModel.selectedPayType === PaymentType.PAY_ALL
    ) {
      this.paymentService.showFullScreenModal(
        this.i18n.cardModal.payTodayAllAsyncPayment,
        fullscreenModalConfig
      );
    }

    if (
      this.viewModel.paymentTime === PaymentType.PAY_TODAY_FEE &&
      this.viewModel.selectedPayType === PaymentType.PAYMENT
    ) {
      this.paymentService.showFullScreenModal(
        this.i18n.cardModal.payTodayAmountAsyncPayment,
        fullscreenModalConfig
      );
    }

    if (
      this.viewModel.paymentTime === PaymentType.PAY_TODAY_FEE &&
      this.viewModel.selectedPayType === PaymentType.PAY_TODAY_FEE
    ) {
      this.paymentService.showFullScreenModal(
        this.i18n.cardModal.payTodayFeeAsyncPayment,
        fullscreenModalConfig
      );
    }

    if (
      this.viewModel.paymentTime === PaymentType.PAY_TODAY_FEE &&
      this.viewModel.selectedPayType === PaymentType.PAY_ALL
    ) {
      this.paymentService.showFullScreenModal(
        this.i18n.cardModal.payTodayAllAsyncPayment,
        fullscreenModalConfig
      );
    }
  }

  private showMessageSuccess(i18n: Record<string, string>, customKey: string) {
    const payOverdueFeeKey = `${PaymentType.OVERDUE_FEE}-${PaymentType.OVERDUE_FEE}`;
    const { total, totalPaid } = this.viewModel.credit.installment;

    if (payOverdueFeeKey === customKey && this.viewModel.installmentPending === total - totalPaid) {
      this.paymentService.showCardModal(this.i18n.cardModalParachute.payOverdueLastFees);

      return;
    }

    if (payOverdueFeeKey === customKey && this.viewModel.installmentPending === 1) {
      this.i18n.cardModalParachute.payOverdueFee.content = createFormat(
        this.i18n.cardModalParachute.payOverdueFee.content,
        [this.datePipe.transform(this.viewModel.credit.installment.next.date)]
      );

      this.paymentService.showCardModal(this.i18n.cardModalParachute.payOverdueFee);

      return;
    }

    if (payOverdueFeeKey === customKey && this.viewModel.installmentPending > 1) {
      this.paymentService.showCardModal(this.i18n.cardModalParachute.payOverdueFees);

      return;
    }

    if (this.viewModel.selectedPayType === PaymentType.PAY_ALL) {
      this.paymentService.showCardModal(this.i18n.cardModalParachute.payOverdueAll);

      return;
    }

    this.paymentService.showCardModal(i18n);
  }

  private updateBackpackIcon() {
    this.creditEventsHandler.emitDisbursedEvent({
      creditType: CreditType.PARACHUTE,
      hasCredits: true,
      isRescheduled: this.viewModel.credit.isRescheduled,
      isExpired: this.viewModel.credit.isExpired,
    });
  }

  private setAnalyticsIntentionToPay(error?: CoreException) {
    let eventName = this.config.analytics.intentionToPay.name.cleverTap;

    const isPayTodayFee = this.viewModel.paymentTime === PaymentType.PAY_TODAY_FEE;
    const nextInstallmentAt = this.viewModel.credit.installment.next.date;

    let params = this.config.analytics.intentionToPay.params.cleverTap;
    let paramsValue = [
      getFormattedDateTimes(),
      isPayTodayFee,
      getFormattedDateTimes(nextInstallmentAt),
      this.viewModel.credit.balance,
      this.viewModel.credit.installment.next.value,
      this.viewModel.credit.installment.total - this.viewModel.credit.installment.totalPaid,
    ];

    if (error) {
      eventName = this.config.analytics.intentionToPay.error;
      params = params.concat(this.config.analytics.intentionToPay.params.error);
      paramsValue = paramsValue.concat([error.code, error.message]);
    }

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(params, paramsValue),
      },
    });
  }
}
