import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';
import { FlowType } from '@nequi/features/commons/core';

import { BaseConfig } from '../../middlewares/base.config';
import { MiddlewareModuleName } from '../../middlewares/base.middleware';
import { ParachuteMiddleware } from '../../middlewares/parachute.middleware';
import { ParachuteConfig } from './config/parachute.config';
import { AdjustInstallmentsPage } from './pages/adjust-installments/adjust-installments.page';
import { ChangeLiveInsurancePage } from './pages/change-live-insurance/change-live-insurance.page';
import { PaymentDetailPage } from './pages/payment-detail/payment-detail.page';
import { PaymentHomePage } from './pages/payment-home/payment-home.page';

const routes: Routes = [
  {
    path: '',
    canActivate: [ParachuteMiddleware],
    data: {
      moduleName: MiddlewareModuleName.PARACHUTE,
      bottomSheetKey: BaseConfig.parachute.bottomSheet,
      redirectToPayment: BaseConfig.routes.parachutePayment,
      redirectToPreapproved: BaseConfig.routes.parachutePreapproved,
      flowType: FlowType.PARACHUTE,
    },
  },
  {
    path: 'payment-home',
    component: PaymentHomePage,
  },
  {
    path: 'adjust-installments',
    component: AdjustInstallmentsPage,
  },
  {
    path: 'payment-detail',
    component: PaymentDetailPage,
  },
  {
    path: 'change-live-insurance',
    component: ChangeLiveInsurancePage,
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(
      I18nModuleResolver.setup(routes, [
        ParachuteConfig.i18n.moduleKey,
        ParachuteConfig.i18n.contractModuleKey,
      ])
    ),
  ],
  exports: [RouterModule],
})
export class ParachuteRoutingModule {}
