import { RescheduleType } from '@nequi/features/credits/core';
import { ConfirmInfoPocket, ConfirmInfoPocketItem } from '@nequi/ui';

import { BuildItemsParams } from '../../../../params/build-items.params';

export class AdjustInstallmentsViewModel {
  public hasError = false;
  public bussyIndicatorError: string;
  public rescheduleType: RescheduleType;

  public buildItems(params: BuildItemsParams): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    const pocket: ConfirmInfoPocket = {
      isPocket: true,
      title: params.messages.items.pocket,
      name: params.pocketInfo.name,
      image: params.pocketInfo.imageUrl,
      icon: params.pocketInfo.iconUrl,
    };

    let items: Array<ConfirmInfoPocketItem | ConfirmInfoPocket> = [
      {
        label: params.messages.items.amount,
        value: params.currencyPipe.transform(params.paymentAmount),
      },
    ];

    if (this.rescheduleType === RescheduleType.INSTALLMENTS_NUMBER) {
      items = items.concat([
        {
          label: params.messages.items.adjustTypeNumber.label,
          value: params.messages.items.adjustTypeNumber.value,
        },
      ]);
    }

    if (this.rescheduleType === RescheduleType.INSTALLMENTS_AMOUNT) {
      items = items.concat([
        {
          label: params.messages.items.adjustTypeAmount.label,
          value: params.messages.items.adjustTypeAmount.value,
        },
      ]);
    }

    items.push(
      {
        label: params.messages.items.nextPayment.label,
        value: params.datePipe.transform(params.nextFeeDate),
        helpMessage: params.messages.items.nextPayment.message,
      },
      pocket
    );

    return items;
  }
}
