import { Component, OnInit } from '@angular/core';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { ViewDetailItem } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { ChangeLiveInsuranceConfig } from './change-live-insurance.config';

@Component({
  selector: 'app-change-live-insurance',
  templateUrl: './change-live-insurance.page.html',
  styleUrls: ['./change-live-insurance.page.scss'],
})
export class ChangeLiveInsurancePage implements OnInit {
  public config = ChangeLiveInsuranceConfig;
  public isScreenShotTaken = false;
  public infoItems: Array<ViewDetailItem>;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider
  ) {}

  public ngOnInit() {
    this.loadData();
  }

  public goBack(): void {
    this.navigatorProvider.back();
  }

  private loadData() {
    const i18nItems = this.translateProvider.get(this.config.i18n.items);

    this.infoItems = [
      {
        label: i18nItems.send.title,
        description: i18nItems.send.description,
      },
      {
        label: i18nItems.check.title,
        description: createFormat(i18nItems.check.description, [
          this.config.cssClass,
          this.config.link,
        ]),
      },
      {
        label: i18nItems.answer.title,
        description: i18nItems.answer.description,
      },
    ];
  }
}
